import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function priceToFreeCashFlowRatioTtm(tableName: string) {
  const title = "P/FCF Ratio (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["priceToFreeCashFlowRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Price per share vs. free cash flow.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about price to free cash flow ratio?`,
              value:
                info.row.original.financialData.companyRatios?.data?.priceToFreeCashFlowRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.priceToFreeCashFlowRatioTtm}
        />
      )
    },
  })
}

export { priceToFreeCashFlowRatioTtm }
